.backdrop {
  z-index: 2;
  background: rgba(0, 0, 0, 0.7);
  height: 100vh;
  width: 100%;
  position: absolute;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
}

.modal {
  bottom: 0;
  width: 30em;
  height: 34em;
  z-index: 3;
  background: #ba9999;
  background: rgba(220, 171, 81, 0.9);
  box-shadow: 0px -4px 30px rgba(0, 0, 0, 0.08);
  border-radius: 34px 34px 34px 34px;
  padding: 14px 16px 16px 16px;
  text-align: center;
  padding-left: 6%;
  padding-right: 6%;
}

.title {
  font-family: inherit;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  color: #222222;
  padding-top: 16px;
}

@media (max-width: 500px) {
  .modal {
    margin: 0 15px;
  }
}
